<template>
  <div class="home">
    <div class="headers">
      <Header></Header>
      <ly-tabs
        v-model="value"
        :activeColor="activeColor"
        @change="handleChange"
      >
        <ly-tab-item
          v-for="item in items"
          :key="item.id"
          :name="item.id"
          :title="item.label"
        />
      </ly-tabs>
    </div>
    <section ref="wrapper">
      <div>
        <div v-for="(item, index) in newData" :key="index">
          <Swiper
            v-if="item.type == 'swiperList'"
            :swiperList="item.data"
          ></Swiper>
          <Icons v-if="item.type == 'iconsList'" :iconsList="item.data"></Icons>
          <Recommend
            v-if="item.type == 'recommendList'"
            :recommendList="item.data"
          ></Recommend>
          <Like v-if="item.type == 'likeList'" :likeList="likeList"></Like>
          <!-- :likeList="item.data" -->
          <Ad v-if="item.type == 'adList'" :adList="item.data"></Ad>
        </div>
      </div>
    </section>
    <Tabber></Tabber>
  </div>
</template>

<script>
import Tabber from "../components/common/Tabber.vue";
import Header from "@/components/Home/Header.vue";
import Swiper from "@/components/Home/Swiper.vue";
import Icons from "@/components/Home/Icons.vue";
import Like from "@/components/Home/Like.vue";
import Ad from "@/components/Home/Ad.vue";
import BScroll from "better-scroll"; //页面滚动插件
import Recommend from "@/components/Home/Recommend.vue";
import { getHomeData, getOtherData, likeList } from "@/api/home";
export default {
  name: "Home_vue",
  components: {
    Tabber,
    Header,
    Swiper,
    Icons,
    Recommend,
    Like,
    Ad,
  },
  data() {
    return {
      value: 0,
      items: [],
      newData: [],
      adList: [],
      activeColor: "#b0352f",
      likeList: [],
    };
  },
  created() {
    this.getData();
  },
  methods: {
    async getData() {
      let res = await getHomeData();
      if (res.data.code == 200) {
        this.items = Object.freeze(res.data.data.topBar);
        this.newData = Object.freeze(res.data.data.data);
      }
      let res1 = await likeList();
      if (res1.data.code == 200) {
        this.likeList = res1.data.data.results;
      }
      //当dom都执行完毕了在去加载
      this.$nextTick(() => {
        new BScroll(this.$refs.wrapper, {
          movable: true,
          zoom: true,
          click: true,
        });
      });
    },
    async otherData(value) {
      let res = await getOtherData(value);
      if (res.data.code == 200) {
        this.newData = Object.freeze(res.data.data);
      }
      this.$nextTick(() => {
        new BScroll(this.$refs.wrapper, {
          movable: true,
          zoom: true,
        });
      });
    },
    handleChange(item) {
      if (item != 0) {
        this.otherData(item);
      } else {
        this.getData();
      }
    },
  },
};
</script>

<style scoped>
.home {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.headers {
  width: 100%;
  height: 2.9rem;
  position: fixed;
  left: 0;
  top: 0;
}
::v-deep .ly-tabbar {
  box-shadow: none;
}
section {
  margin-top: 3rem;
  flex: 1;
  overflow: hidden;
  margin-top: 2.3rem;
}
</style>
