import request from "./request";
export const getHomeData = () => {
  return request({
    url: "/api/index_list/0/data/1",
    method: "get",
  });
};
export const getOtherData = (value) => {
  return request({
    url: `/api/index_list/${value}/data/1`,
    method: "get",
  });
};

export const getShopList = (name, orderBy) => {
  return request({
    url: "/api/goods/shopList",
    method: "get",
    params: {
      name,
      orderBy,
    },
  });
};

//获取猜你喜欢列表
export const likeList = () => {
  return request({
    url: "/api/home/likelist",
    method: "get",
  });
};
