<template>
  <div class="like">
    <Card>
      <span>猜你喜欢</span>
    </Card>
    <ul>
      <li
        v-for="item in likeList"
        :key="item.id"
        @click="goDetailPage(item.id)"
      >
        <h2>
          <img v-lazy="item.imgUrl" />
        </h2>
        <h3>{{ item.name }}</h3>
        <div>
          <span>￥</span>
          <b>{{ item.price }}</b>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Vue from "vue";
import Card from "./Card.vue";
import { Lazyload } from "mint-ui";
Vue.use(Lazyload);
export default {
  name: "like-vue",
  components: {
    Card,
  },
  props: ["likeList"],
  methods: {
    goDetailPage(id) {
      this.$router.push({
        path: "/detail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style scoped>
.like ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: space-between;
}
.like ul li {
  float: left;
  padding: 0.16rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 46%;
}
.like img {
  width: 4.694rem;
  height: 4.694rem;
}
.like h3 {
  padding: 0 0.16rem;
  font-size: 0.36rem;
  font-weight: 500;
  width: 93%;
  font-size: 0.42666rem;
  color: #222;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}
.like ul li > div {
  width: 93%;
  padding: 0.16rem;
  text-align: left;
}
.like ul li > div span {
  font-size: 0.32rem;
}
.like ul li > div b {
  font-size: 0.427rem;
  font-weight: 600;
  color: #ff0000;
}
image[lazy="loading"] {
  width: 40px;
  height: 300px;
  margin: auto;
}
</style>
