<template>
  <div class="swiper">
    <swiper :options="swiperOption" class="swiper-main">
      <swiper-slide v-for="item in swiperList" :key="item.id">
        <img :src="item.imgUrl" id="swiper-img"
      /></swiper-slide>
    </swiper>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
export default {
  name: "swiper-vue",
  props: ["swiperList"],
  data() {
    return {
      swiperOption: {
        autoplay: 3000,
        speed: 1000,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
      },
    };
  },
};
</script>

<style scoped>
.swiper {
  position: relative;
}
.swiper-main {
  width: 100%;
  height: 4.4rem;
}
.swiper-container {
  width: 100%;
  height: 4.4rem;
}
#swiper-img {
  width: 100%;
  height: 4.4rem;
}
.swiper-pagination {
  position: absolute;

  width: 100%;
  bottom: 20px;
}
::v-deep .swiper-pagination-bullet-active {
  background-color: #b0352f;
}
</style>
