const request = axios.create({
  baseURL: "",
  timeout: 5000,
});
request.interceptors.request.use(function (config) {
  Indicator.open("加载中...");
  config.headers["token"] = getToken(); //如果本地存储中有token则请求头携带token，如果没有token则携带‘’
  return config;
});
//设置响应拦截器
request.interceptors.response.use(
  (response) => {
    setTimeout(() => {
      Indicator.close();
    }, 200);

    return response;
  },
  (error) => {
    return Promise.reject(error); //终止promise链
  }
);
//获取本地存储中的token
function getToken() {
  let teaUi = JSON.parse(localStorage.getItem("teaUserInfo"));

  if (teaUi) {
    return teaUi.results[0].token;
  } else {
    return "";
  }
}
export default request;
