import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
Vue.use(VueRouter);
const routes = [
  {
    path: "/home",
    name: "Home",
    component: Home,
  },
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/list",
    name: "List",
    component: () => import("../views/List.vue"),
  },
  {
    path: "/cart",
    name: "Cart",
    component: () => import("../views/Cart.vue"),
  },
  {
    path: "/my",
    name: "My",
    component: () => import("../views/My.vue"),
  },
  {
    path: "/mymessage",
    name: "MyMessage",
    component: () => import("../views/MyMessage.vue"),
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/login/login.vue"),
  },
  {
    path: "/register",
    name: "Register",
    component: () => import("../views/login/register.vue"),
  },
  {
    path: "/paysuccess",
    name: "PaySuccess",
    component: () => import("../views/PaySuccess.vue"),
  },
  {
    path: "/passwordLogin",
    name: "PasswordLogin",
    component: () => import("../views/login/PasswordLogin.vue"),
  },
  {
    path: "/order",
    name: "Order",
    component: () => import("../views/Order.vue"),
  },
  {
    path: "/address",
    name: "Address",
    component: () => import("../views/path/Address.vue"),
    children: [
      {
        path: "/",
        name: "AddressIndex",
        component: () => import("../views/path/Address-index.vue"),
      },
      {
        path: "addPathList",
        name: "AddPathList",
        component: () => import("../views/path/addPathList.vue"),
      },
    ],
  },
  {
    path: "/search",
    name: "Search",

    component: () => import("../views/Search.vue"),
    children: [
      {
        path: "list",
        name: "searchList",
        component: () => import("../views/search/Search-list.vue"),
      },
      {
        path: "/",
        name: "index",
        component: () => import("../views/search/Search-index.vue"),
      },
    ],
  },
  {
    path: "/detail",
    name: "Detail",
    component: () => import("../views/Detail.vue"),
  },
  {
    path: "/findPassword",
    name: "FindPassword",
    component: () => import("../views/login/FindPassword.vue"),
  },
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes,
});
router.beforeEach((to, from, next) => {
  let nextRoute = [
    "Cart",
    "Order",
    "AddPathList",
    "AddressIndex",
    "Address",
    "PaySuccess",
    "MyMessage",
  ];
  //是否是登录状态
  let userInfo = JSON.parse(localStorage.getItem("teaUserInfo"));
  if (nextRoute.indexOf(to.name) != -1 && userInfo?.success) {
    next();
  } else if (nextRoute.indexOf(to.name) == -1) {
    next();
  } else if (!userInfo?.success) {
    router.push("/login");
  }
});
export default router;
