import { USER_LOGIN } from "./mutations-types.js";
export default {
  state: {
    loginStatus: false, //用户的登录状态
    userInfo: {},
  },
  getters: {},
  mutations: {
    //设置
    [USER_LOGIN](state, user) {
      state.loginStatus = user.success;
      state.userInfo = user.results[0];
      localStorage.setItem("teaUserInfo", JSON.stringify(user));
    },
    //读取
    INIT_USER(state) {
      let userInfo = JSON.parse(localStorage.getItem("teaUserInfo"));
      if (userInfo) {
        state.loginStatus = userInfo.success;
        state.userInfo = userInfo.results[0];
      }
    },
  },
  actions: {},
};
