<template>
  <div class="tabber">
    <ul>
      <li
        v-for="(item, index) in this.routerList"
        :key="index"
        @click="switchTab(item.path)"
      >
        <img
          :src="$route.path.includes(item.path) ? item.selected : item.active"
          :alt="item.title"
        />
        <span :class="$route.path.includes(item.path) ? 'active' : ''">
          {{ item.title }}
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "Tabber_vue",
  data() {
    return {
      routerList: [
        {
          title: "首页",
          path: "/home",
          active: "/images/home.png",
          selected: "/images/home_checked.png",
        },
        {
          title: "列表",
          path: "/list",
          active: "/images/list.png",
          selected: "/images/my_checked .png",
        },
        {
          title: "购物车",
          path: "/cart",
          active: "/images/shop.png",
          selected: "/images/shop_checked.png",
        },
        {
          title: "我的",
          path: "/my",
          active: "/images/my.png",
          selected: "/images/my_checked.png",
        },
      ],
    };
  },
  methods: {
    switchTab(path) {
      //判断是否为同一个路由
      if (this.$route.path == path) {
        return;
      } else if (path == "/cart") {
        if (JSON.parse(localStorage.getItem("teaUserInfo"))?.success) {
          this.$router.replace("/cart");
        } else {
          Toast("您还未登录");
        }
      } else {
        this.$router.replace(path);
      }
    },
  },
};
</script>

<style scoped>
.tabber {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1.6rem;
  background-color: #fff;
  z-index: 999;
}
.tabber ul {
  display: flex;
}
.tabber ul li {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: 0.4266rem;
  text-align: center;
}
.tabber ul li img {
  width: 0.83rem;
  height: 0.83rem;
}
.active {
  color: red;
}
</style>
